import React, { useState, useContext, Fragment } from "react";
import { FirebaseContext } from "../firebase";

import { Link } from "react-router-dom";

const SignUp = (props) => {
  const firebase = useContext(FirebaseContext);

  const data = {
    pseudo: "",
    email: "",
    pwd: "",
    pwd2: "",
  };
  const [loginData, setLoginData] = useState(data);

  const [error, setError] = useState("");

  const handleChange = (e) => {
    setLoginData({ ...loginData, [e.target.id]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const { email, pwd } = loginData;
    firebase
      .signUpUser(email, pwd)
      .then((user) => {
        setLoginData({ ...data });
        props.history.push("/");
      })
      .catch((error) => {
        setError(error);
        setLoginData({ ...data });
      });
  };

  const { pseudo, email, pwd, pwd2 } = loginData;

  const btn =
    pseudo === "" || email === "" || pwd === "" || pwd !== pwd2 ? (
      <button className="mt-3" disabled>
        Inscription
      </button>
    ) : (
      <button className="mt-3">Inscription</button>
    );

  //gestion des erreurs
  const errorMsg = error !== "" && <span>{error.message}</span>;

  return (
    <Fragment>
      <div className="m-5 w-100">
        {errorMsg}
        <h1>Inscription</h1>
        <form onSubmit={handleSubmit} className="d-flex mt-5">
          <div className="col col-5 d-flex flex-column ">
            <label className="h4 text-white mt-3">Pseudo</label>
            <input
              onChange={handleChange}
              value={pseudo}
              type="text"
              placeholder="Pseudo"
              id="pseudo"
              autoComplete="off"
              required
            />
            <label className="h4 text-white mt-3">Email</label>
            <input
              onChange={handleChange}
              value={email}
              type="email"
              placeholder="Email"
              id="email"
              required
            />
          </div>

          <div className="col col-5 d-flex flex-column ">
            <label className="h4 text-white mt-3">Mot de passe</label>
            <input
              onChange={handleChange}
              value={pwd}
              type="password"
              placeholder="Mot de passe"
              id="pwd"
              autoComplete="off"
              required
            />
            <label className="h4 text-white mt-3">
              Confirmez votre mot de passe
            </label>
            <input
              onChange={handleChange}
              value={pwd2}
              type="password"
              placeholder="Mot de passe"
              id="pwd2"
              autoComplete="off"
              required
            />
            {btn}
          </div>
        </form>
        <div>
          <Link to="/login">Déjà inscrit? Connectez vous</Link>
        </div>
      </div>
    </Fragment>
  );
};

export default SignUp;
