import React, { useState, useEffect, useContext, Fragment } from "react";

import { Link } from "react-router-dom";
import { FirebaseContext } from "../firebase";

const Login = (props) => {
  const firebase = useContext(FirebaseContext);

  const [email, setEmail] = useState("");
  const [pwd, setPwd] = useState("");
  const [btn, setBtn] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    if (pwd.length > 5 && email !== "") {
      setBtn(true);
    } else if (btn) {
      setBtn(false);
    }
  }, [pwd, email, btn]);

  const handleSubmit = (e) => {
    e.preventDefault();

    firebase
      .loginUser(email, pwd)
      .then((user) => {
        setEmail("");
        setPwd("");
        props.history.push("/Connected");
      })
      .catch((error) => {
        setError(error);
        setEmail("");
        setPwd("");
      });
  };

  return (
    <Fragment>
      <div className="col col-6 m-5">
        {error !== "" && <span>{error.message}</span>}
        <h1>Connexion</h1>
        <form
          className="d-flex align-content-center flex-column w-75 mt-5"
          onSubmit={handleSubmit}
        >
          <label className="h4 text-white">Email</label>
          <input
            onChange={(e) => setEmail(e.target.value)}
            value={email}
            type="email"
            placeholder="Email"
            required
          />
          <label className="h4 text-white mt-5">Mot de passe</label>
          <input
            onChange={(e) => setPwd(e.target.value)}
            value={pwd}
            type="password"
            placeholder="Mot de passe"
            autoComplete="off"
            required
          />
          {btn ? (
            <button className="btn mt-5 w-50">Connexion</button>
          ) : (
            <button disabled>Connexion</button>
          )}
        </form>
        <div>
          <Link to="/signup">Pas encore inscrit? Inscrivez vous</Link>
        </div>
      </div>
    </Fragment>
  );
};

export default Login;
